import { Component, OnInit } from '@angular/core';
import { DetailsFormBase } from '../../../../../shared/ui/objects-view/details-form-base';
import { IApiUserModel } from '../../../../../shared/fleio-api/cloud/model/api-user.model';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfigService } from '../../../../../shared/config/config.service';
import { ApiUsersApiService } from '../../../../../shared/fleio-api/cloud/api-user/api-users-api.service';
import { Observable, of } from 'rxjs';
import { IActionResult } from '../../../../../shared/ui/objects-view/interfaces/actions/action-result';
import { IApiUserCreateOptionsModel } from '../../../../../shared/fleio-api/cloud/model/api-user-create-options.model';

@Component({
  selector: 'app-api-user-edit-form',
  templateUrl: './api-user-edit-form.component.html',
  styleUrls: ['./api-user-edit-form.component.scss']
})
export class ApiUserCreateFormComponent extends DetailsFormBase<IApiUserModel> implements OnInit {
  createOptions: IApiUserCreateOptionsModel;
  apiUserForm = this.formBuilder.group({
    name: ['', Validators.required],
    default_project: ['', Validators.required],
    password: ['', Validators.required],
    description: [''],
  });
  loading = false;
  constructor(
    private formBuilder: FormBuilder, private apiUsersApiService: ApiUsersApiService, private router: Router,
    private config: ConfigService, private activatedRoute: ActivatedRoute
  ) {
    super();
  }

  private saveUser(): Observable<IActionResult> {
    const value = this.apiUserForm.value as any;
    value.default_project = value.default_project.id;
    this.loading = true;
    this.createOrUpdate(this.apiUsersApiService, value).subscribe(() => {
      this.loading = false;
      this.router.navigateByUrl(
        this.config.getPrevUrl('cloud/api-users')
      ).catch(() => {
        this.loading = false;
      });
    });
    return of(null);
  }

  private displayProjectFn(project) {
    if (project && project.id) {
      return project.name + ' ' + project.id;
    }
    return '';
  }

  ngOnInit() {
    this.objectController.actionCallback = () => this.saveUser();
    this.createOptions = this.activatedRoute.snapshot.data.createOptions;
    if (this.object) {
      this.apiUserForm.patchValue(this.object);
    }
  }

}
